import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';
import { CustomCenterHttpService } from '../http/custom-center.http.service';
import { ActivatedRoute } from '@angular/router';
import * as $ from 'jquery';
import { AppproResultBean, BussinessMap, VetoBean, SubApproveMapBean } from './bean/business-information.bean';
import { ReportBean } from 'app/pages/tzb/custom/custom-view/report/bean/report.bean';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { API } from '../http/custom-center.http.constant'
import { TZB_HTTP_CUS1 } from 'app/@uisftech/common/service/http.constant';
import * as _ from 'lodash';
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';

@Component({
    selector: "customer-distribution",
    templateUrl: './customer-distribution.component.html',
    styleUrls: ['./customer-distribution.component.scss'],
    providers: [Commfunc, CustomCenterHttpService, ConfirmationService]
})
export class CustomerDistribution {
    cardType: string;
    TZB_HTTP_CUS1: any = TZB_HTTP_CUS1;
    reportBean: ReportBean = new ReportBean();
    msgs: Message[] = [];
    personName: string;
    vetoFirsts: any = [];//
    orgId: string;
    tabIndex: any = '0';
    display: boolean;
    vetoScrond: any;
    backReason: any;
    TurnReson: any = [];//
    state: any;
    informationPerfection: any;
    code: any;
    personPage: boolean = false;
    userInValue: any;
    vetoScrondOne: any = [];
    examineDisplay: any;
    extableData: any;
    ex: any = [];
    resultList: any = {};
    userId: string;
    bussinessMap: BussinessMap = new BussinessMap();
    appproResultBean: AppproResultBean = new AppproResultBean();
    vetoBean: VetoBean = new VetoBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean()
    dataList: any = {};
    personVeto: any = [];
    financeVeto: any = [];
    manageVeto: any = [];
    serveVeto: any = [];
    otherVeto: any = [];
    vetoOption: any = [];
    otherInfo: string;
    reportList: any;
    inValueReportId: any;
    reportCustNm: any;
    //
    postPage: boolean;
    postName: any;
    //
    personDis: boolean;
    branch: any;
    custNo: any;
    //
    cardMsg: any;
    isBack: boolean = false;
    cusId: string;
    customerDistribute: string;
    isHistory: boolean = false;

    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    personShow: boolean = false;
    personId_new: string;
    personName_new: string;
    inValue: any;
    inValueOrg: any;
    comments: string;
    bzIsshow: boolean = false;
    votePost: string = '';
    postOption: any;
    postNameVeto: string = '';
    dataMapParam: any;
    reasonOne: string = ''; // 
    reasonTwo: string = ''; // 
    zjs: boolean = true;
    hasPermiss: boolean = true;
    businessId: string;//
    flag: boolean = true;//
    debtValue:any;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public customCenterHttpService: CustomCenterHttpService,
        public activatedRoute: ActivatedRoute,
        public httpService: CusViewHttpService,
        public commonHttpService: CommonHttpService,

    ) {

        this.resultList = this.activatedRoute.snapshot.params;
        if (this.commfunc.getSessionDataCH('distributeParams')) {
            this.dataMapParam = JSON.parse(this.commfunc.getSessionDataCH('distributeParams'));
            //
            debugger
            
            if(this.dataMapParam.acountNo){
                let cardType = this.dataMapParam.acountNo.slice(13, 14);
                //'0''2'
                this.dataMapParam['cardType'] = cardType == '1' ? '0' : '2';
                this.dataMapParam['loanAcctNo'] = this.dataMapParam.acountNo;
            }
        }
        if (this.resultList.markFlag == '2') {
            this.isHistory = true;
        }
        if (this.resultList.perssion != '?auth=htzx') {
            this.zjs = true;
        } else {
            this.zjs = false;
        }
        if (this.dataMapParam &&
            this.dataMapParam != '' &&
            this.dataMapParam != undefined &&
            this.dataMapParam != null &&
            this.dataMapParam != 'undefined') {
            this.dataList = this.dataMapParam;
        }
        // dataSource=='01'  dataSource=='02' '03' dataMapParam
        if (this.resultList.dataSource == '01' || this.resultList.dataSource == '02') {
            let infoBean = {
                dataSource: this.resultList.dataSource, // 
                requestSeqNo: this.resultList.requestSeqNo, // ID
                workEffortId: this.resultList.workEffortId, //
            }
            this.httpService.queryCustomerInfoByWorkEffCondition(infoBean).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.workEffortList && data.workEffortList.length > 0) {
                        this.dataList['fieldList'] = data.workEffortList[0].fieldList;
                        this.queryBasicInfo();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            })
        } else if (this.resultList.dataSource == '03') {
            this.queryBasicInfo();
        } else if (this.resultList.dataSource == '04') {
            this.queryBasicInfo();
        }
        this.codeValues() //
        this.TurnReson = this.code['TurnReson'];//
        this.vetoFirsts = this.code['VETOFIRDTS'];//
        this.personVeto = this.code['personVeto'];//-
        this.financeVeto = this.code['financeVeto'];//
        this.manageVeto = this.code['manageVeto'];//
        this.serveVeto = this.code['serveVeto'];//
        this.otherVeto = this.code['otherVeto'];// 

        if (this.resultList.taskActors && this.resultList.taskActors != '') {
            this.businessId = this.resultList.taskActors.split(',')[0] + ',' + this.resultList.taskId;
        }
    }

    ngOnInit() {
        this.vetoBean.oneVetoReason = this.vetoFirsts[0].value;
        this.tabIndex = '0';
        // 
        this.debtValue = {
           isCreditCcm :false
        }
        // 
        let user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (user.extParam && user.extParam.orgId) {
            this.orgId = user.extParam.orgId
        };
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;

        let commonHeaderss = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let postIdparam = commonHeaderss.extParam['postId'];

        let Postparam = {
            pageSize: '999999',
            postId: postIdparam,

        }
        this.commonHttpService.queryVaguePostList(Postparam).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.postList && data.postList.length > 0) {
                    this.postNameVeto = data.postList[0].postName;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })

        // 
        let userPermissin = {
            tellerId: this.userId,
        }
        this.commonHttpService.queryPermissionByTellerId(userPermissin).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.permissionsMap && data.permissionsMap['loanPermission'] == '1') {
                    this.hasPermiss = true
                } else {
                    this.hasPermiss = false
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    queryBasicInfo(){

        if (this.dataList['taskType'] != '08') {
            if (this.dataList['dataSourceId'] == 'BR00A') {
                this.inValueOrg = 'T0000';//
            } else if (this.dataList['dataSourceId'] == 'BR00B') {
                this.inValueOrg = 'C0000';//
            }
        } else {
            this.inValueOrg = '0000';//
        }
        this.cardMsg = {};
        if (this.dataList.cusAssignFlag) {
            this.isBack = true;
        } else {
            this.isBack = false;
        }
        if (this.dataList.dataSource == '01') {
            this.dataList['fieldList'].forEach(e => {
                if (e['key'] == 'custNo' || e['key'] == 'custId') {
                    this.cardMsg['custNo'] = e['value'];
                }
                if (e['key'] == 'custName') {
                    this.cardMsg['custName'] = e['value'];
                }
                if (e['key'] == 'globalId' || e['key'] == 'idNo') {
                    this.cardMsg['cetId'] = e['value'];
                }
                if (e['key'] == 'idType' || e['key'] == 'globalType') {
                    this.cardMsg['cretType'] = e['value'];
                }
            })
        } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '10') {
            this.cardMsg['custName'] = this.dataList['fieldList'][0]['custName'];
            if (this.dataList['fieldList'][0]['custId']) {
                this.cardMsg['custNo'] = this.dataList['fieldList'][0]['custId']
            } else if (this.dataList['fieldList'][0]['custNo']) {
                this.cardMsg['custNo'] = this.dataList['fieldList'][0]['custNo']
            }
            if (this.dataList['fieldList'][0]['globalId']) {
                this.cardMsg['cetId'] = this.dataList['fieldList'][0]['globalId']
            } else if (this.dataList['fieldList'][0]['idNo']) {
                this.cardMsg['cetId'] = this.dataList['fieldList'][0]['idNo']
            }
            if (this.dataList['fieldList'][0]['idType']) {
                this.cardMsg['cretType'] = this.dataList['fieldList'][0]['idType']
            } else if (this.dataList['fieldList'][0]['globalType']) {
                this.cardMsg['cretType'] = this.dataList['fieldList'][0]['globalType']
            }
        } else if (this.dataList.dataSource == '02' && this.dataList['taskType'] == '11') {
            this.cardMsg['custName'] = this.dataList['fieldList'][0]['custName'];
            if (this.dataList['fieldList'][0]['custId']) {
                this.cardMsg['custNo'] = this.dataList['fieldList'][0]['custId']
            } else if (this.dataList['fieldList'][0]['custNo']) {
                this.cardMsg['custNo'] = this.dataList['fieldList'][0]['custNo']
            }
            if (this.dataList['fieldList'][0]['globalId']) {
                this.cardMsg['cetId'] = this.dataList['fieldList'][0]['globalId']
            } else if (this.dataList['fieldList'][0]['idNo']) {
                this.cardMsg['cetId'] = this.dataList['fieldList'][0]['idNo']
            }
            if (this.dataList['fieldList'][0]['idType']) {
                this.cardMsg['cretType'] = this.dataList['fieldList'][0]['idType']
            } else if (this.dataList['fieldList'][0]['globalType']) {
                this.cardMsg['cretType'] = this.dataList['fieldList'][0]['globalType']
            }
        } else if (this.dataList.dataSource == '03') {
            this.cardMsg['custName'] = this.dataList['custName'];
            if (this.dataList['custId']) {
                this.cardMsg['custNo'] = this.dataList['custId']
            } else if (this.dataList['custNo']) {
                this.cardMsg['custNo'] = this.dataList['custNo']
            }
            if (this.dataList['globalId']) {
                this.cardMsg['cetId'] = this.dataList['globalId']
            } else if (this.dataList['idNo']) {
                this.cardMsg['cetId'] = this.dataList['idNo']
            }
            if (this.dataList['idType']) {
                this.cardMsg['cretType'] = this.dataList['idType']
            } else if (this.dataList['globalType']) {
                this.cardMsg['cretType'] = this.dataList['globalType']
            }
        } else if (this.dataList.dataSource == '04') {
            let arr = this.dataList.fieldList[0].resultList[0];
            this.cardMsg['custNo'] = arr['custNo'];
            this.cardMsg['custName'] = arr['custName'];
            this.cardMsg['cetId'] = arr['idenNum'];
            this.cardMsg['cretType'] = arr['idenType'];
            this.dataList['globalType'] = arr['idenType']; // 
            this.dataList['globalId'] = arr['idenNum']; // 
            this.dataList['loanPurpuse'] = arr['idenNum']; // 
            this.dataList['applyDate'] = arr['groupAcDate']; // 
        }
    }

    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }

    handleChange(e) {
        this.tabIndex = e.index;
        if (this.tabIndex == '0') {

        } else if (this.tabIndex == '1') {
            this.queryReportList();
        }
    }

    //
    queryReportList() {

        this.reportBean.custNo = this.cardMsg['custNo'];
        this.httpService.compresiveCreditReportListQry(this.reportBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.reportList && data.reportList.length > 0) {
                    this.inValueReportId = _.clone(data.reportList[0]['reportId']);
                    this.cusId = _.clone(data.reportList[0]['custNo']);
                    this.customerDistribute = '1';
                } else {
                    this.report();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    // ----2018/05/14
    report() {
        let param = { custNo: this.cardMsg['custNo'] }
        let params = this.commfunc.handleParams(param)
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                if (data.returnCode.code == 'success') {
                    if (data.isCreate == "1") {
                        let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
                        _self.creatMainMsg({ custNo: _self.cardMsg['custNo'], modNum: data.updatedNum, custName: _self.cardMsg['custName'], qryUserId: _self.userId, cretType: _self.cardMsg['cretType'], cretNo: _self.cardMsg['cetId'], qryUserName: userName, crdtReportNo: data.crdtReportNo })
                    } else {
                        _self.inValueReportId = data.reportId;
                        _self.reportCustNm = _self.cardMsg['custNo'];
                    }
                } else {
                    _self.msgs = [];
                    _self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            },
            error: function (err) {

            }
        })
    }

    //
    disabledMsgUpside() {
        let params = {
            businessId: this.businessId,
            custNo: this.dataList['custNo']
        };
        this.httpService.disabledMsgUpside(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.disposeSchedule();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    creatMainMsg(param) {
        let params = this.commfunc.handleParams(param);
        this.httpService.createReportMainInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.inValueReportId = _.clone(data.inValueReportIdBak);
                this.cusId = _.clone(data.cusIdBak);
                this.customerDistribute = '1';
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }
    turnPost() {
        if (this.personName == null && this.personName == undefined) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        } else {
            this.postPage = true;
        }
    }

    show(value) {
        this.bussinessMap.specifyNextOpers = undefined;
        this.treeOrgName = undefined;
        this.personName_new = undefined;
        this.subApproveMapBean.info = undefined;
        this.bussinessMap.post = undefined;
        this.subApproveMapBean.result = undefined;

        if (value == '01') {
            this.bussinessMap.approve = '10';
            this.subApproveMapBean.result = '10';
            this.agree();
        } else if (value == '03') {//
            this.bussinessMap.cusAssignFlag = '03';
            this.bussinessMap.approve = '0';
            this.subApproveMapBean.result = '0';
            if (this.resultList.perssion) {
                let ss = this.resultList.perssion;
                let arr = this.resultList.perssion.split('=');
                if (arr.length > 1) {
                    this.bussinessMap['auth'] = arr[1];
                }
            }
            this.agreeClick();
        } else if (value == '04') {//
            this.bussinessMap.cusAssignFlag = '04';
            this.bussinessMap.approve = '1';
            this.subApproveMapBean.result = '1';
            this.state = value;
            this.display = true;
        } else if (value == '2') {//
            this.bussinessMap.approve = '8';
            this.subApproveMapBean.result = '8';
            this.state = value;
            this.display = true;
        } else if (value == '05') {//
            // 
            let user3 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let user4 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
            let orgNamedefault = '';
            let orgIddefault = '';

            if (user3.orgId) {
                orgIddefault = user3.orgId;
            }

            if (user4.orgName) {
                orgNamedefault = user4.orgName;
            }

            this.treeOrgName = orgNamedefault;
            this.treeOrgId = orgIddefault;
            this.inValue = orgIddefault;
            if (this.zjs) {
                this.bussinessMap.approve = '9';
                this.subApproveMapBean.result = '9';
            } else {
                this.bussinessMap.approve = '10';
                this.subApproveMapBean.result = '10';
            }
            this.state = value;
            this.display = true
            this.bussinessMap.cusAssignFlag = this.branch
        }
    }

    //
    agree() {
        this.personDis = true;
    }

    //next
    disposeSchedule() {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        let dataMap = this.dataList;
        dataMap['cusAssignFlag'] = this.branch
        this.bussinessMap.cusAssignFlag = this.branch
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.bussinessMap['dataMap'] = dataMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.resultList['taskId'];
        this.customCenterHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                s['globalSeqNo'] = '';
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers

                }
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
                setTimeout(() => {
                    window.history.go(-1);
                }, 500)
                this.treeOrgName = '';
                this.personName_new = '';
            } else {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    //
    agreeClick() {
        this.branch = '03';
        let param = {
            acceptId: JSON.parse(this.commfunc.getSessionData('commonHeader'))['userId'],
            acceptName: JSON.parse(this.commfunc.getSessionDataCH('chName'))['userName'],
            acceptOrgCode: this.orgId,
            acceptOrgName: JSON.parse(this.commfunc.getSessionDataCH('chName'))['orgName'],
            dataSource: this.dataList['dataSource'],
            workEffortId: this.dataList['workEffortId'],
            workEffortTypeId: this.dataList['workEffortTypeId'],
            requestSeqNo: this.dataList['requestSeqNo'],
            custNo: this.dataList['custNo'],
            dataMap: this.dataList,
        };
        this.customCenterHttpService.acceptTask(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.branch = '03';
                this.disabledMsgUpside();
                this.display = false;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    close() {
        this.state = null;
        this.display = false;
        this.vetoOption = [];
        this.vetoBean.oneVetoReason = '';
        this.vetoBean.twoVetoReason = '';
        this.comments = '';
        this.bzIsshow = false;
        this.otherInfo = '';
    }

    //
    turnStaff() {
        this.personPage = true;
        this.userInValue = '0000';
    }

    //
    perInfor(item) {
        this.bussinessMap.specifyNextOpers = item[0].tellerId;
        this.personName = item[0].tellerName;
        this.postName = null;
        this.personPage = false;
    }

    //
    rurnClick() {
        if (this.treeOrgName == undefined || this.treeOrgName == null || this.treeOrgName == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return
        }
        if (this.personName_new == undefined || this.personName_new == null || this.personName_new == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '!' }];
            return
        }
        if (this.zjs) {
            if (this.subApproveMapBean.info == undefined || this.subApproveMapBean.info == null || this.subApproveMapBean.info == '') {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                return
            }
            if (this.subApproveMapBean.info == '03') {
                if (this.otherInfo == '' || this.otherInfo == undefined || this.otherInfo == null) {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    return
                }
            }
            if (this.subApproveMapBean.info == '03') {
                this.subApproveMapBean.info = this.otherInfo;
            } else {
                this.TurnReson.forEach(element => {
                    if (element.value == this.subApproveMapBean.info) {
                        this.subApproveMapBean.info = element.label;
                    }
                });
            }
            this.subApproveMapBean.info = this.otherInfo;
        }

        this.display = false;
        if (this.flag) {//
            this.disabledMsgUpside();
        } else {
            this.disposeSchedule();
        }
    }

    //
    vetoClick() {
        debugger
        if (this.vetoBean.oneVetoReason == '' || this.vetoBean.oneVetoReason == undefined || this.vetoBean.oneVetoReason == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (this.vetoBean.twoVetoReason == '' || this.vetoBean.twoVetoReason == undefined || this.vetoBean.twoVetoReason == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        // -
        if (this.vetoBean.twoVetoReason == '4110') {
            if (this.comments == '' || this.comments == undefined || this.comments == null) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                return
            }
        }
        //  
        this.vetoFirsts.forEach(element => {
            if (element.value == this.vetoBean.oneVetoReason) {
                this.reasonOne = element.label;
            }
        });
        if (!this.bzIsshow) {// -
            this.vetoOption.forEach(element => {
                if (element.value == this.vetoBean.twoVetoReason) {
                    this.reasonTwo = element.label;
                }
            });
            this.subApproveMapBean.info = this.reasonOne + ',' + this.reasonTwo;
        } else {// -
            this.reasonTwo = this.comments;
            this.subApproveMapBean.info = this.reasonOne + ',' + this.comments;
        }

        this.branch = '04';
        if (this.dataList['dataSource'] == '01') {
            this.vetoBean.applyDate = this.dataList['sysPushDate'];
        } else {
            this.vetoBean.applyDate = this.dataList['applyDate']; // 
        }
        this.vetoBean.voteId = this.userId;// 
        this.vetoBean.voteOrg = this.orgId;// 
        this.vetoBean.votePost = this.postNameVeto,//  
            this.vetoBean.voteReason = this.comments, // 
            this.vetoBean.custNo = this.dataList['custNo']; // 
        this.vetoBean.globalType = this.cardMsg['cretType']; // 
        this.vetoBean.globalId = this.dataList['globalId']; // 
        this.vetoBean.loanPurpuse = this.dataList['loanPurpuse']; // 
        this.vetoBean.taskType = this.dataList['taskType'] // 
        this.vetoBean.workEffortId = this.dataList['workEffortId']; // ID
        this.vetoBean.requestSeqNo = this.dataList['requestSeqNo'], // 
            this.vetoBean.dataSource = this.dataList['dataSource']; // 
        this.vetoBean.bussType = this.resultList['bussType'];// 

        // this.vetoBean.operationLink = this.resultList['bussType'];// 

        this.customCenterHttpService.voteApplicant(this.vetoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList.branch = '04'
                this.disabledMsgUpside();
                this.display = false;
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    vetoChange(event) {
        debugger
        this.comments = '';
        this.bzIsshow = false;
        this.reasonOne = '';
        this.reasonTwo = '';
        if (event.value == '06') {
            this.vetoOption = this.personVeto;
        } else if (event.value == '07') {
            this.vetoOption = this.financeVeto;
        } else if (event.value == '08') {
            this.vetoOption = this.manageVeto;
        } else if (event.value == '09') {
            this.vetoOption = this.serveVeto;
        } else if (event.value == '10') {
            this.vetoOption = this.otherVeto;
        }
        if (event.value == '') {
            this.vetoBean.twoVetoReason = '';
            this.vetoOption = [];
        } else {
            this.vetoBean.twoVetoReason = this.vetoOption[0].value;
        }
    }

    // 
    vetoChangeTwoReson(event) {
        this.comments = '';
        this.reasonTwo = ''
        if (event.value == '4110') {
            this.bzIsshow = true;
        } else {
            this.bzIsshow = false;
        }
    }

    // 
    zjsChange(event) {
        this.otherInfo = '';
    }

    //
    hitClick() {
        let info = this.subApproveMapBean.info;
        if (info == '' || info == null || info == undefined) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
            return
        }
        if (info.trim() == '' || info.trim() == null || info.trim() == undefined) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
            return
        }
        this.display = false;
        let param = {
            userId: this.userId,
            taskId: this.resultList['taskId'],
            tranType: 'B0',
            doResion: '2',
            approveInfo: this.subApproveMapBean.info,
        }
        this.msgs = [];
        this.customCenterHttpService.bpmUserTaskManage(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                commonHeader['globalSeqNo'] = '';
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                this.doUp();
            } else {
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    doUp() {
        setTimeout(() => {
            window.history.go(-1);
        }, 500)
    }

    postInfor(e) {
        this.postName = e[0].postName;
        this.bussinessMap.post = e[0]['postId'];
        this.checkManager();
    }

    checkManager() {
        let param = {
            postId: this.bussinessMap.post
        }
        this.msgs = [];
        this.customCenterHttpService.queryPostById(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                if (data['postInfoList'][0]['managePermission'] != 'Y') {
                    this.branch = '01'
                } else {
                    this.branch = '02'
                }
                this.postPage = false;
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];

            }
        }, err => {
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];

        })
    }

    //
    personDiss(e) {
        this.bussinessMap.specifyNextOpers = e[0]['tellerId'];
        this.personDis = false;
        this.confirmationService.confirm({//
            message: '?',
            accept: () => {
                this.branch = '01';
                this.extableData = true;
                this.disabledMsgUpside();
            }
        });

    }

    //
    checkSerIal(num, num2?) {
        if (num2 && num2 == 1) {//
            if (this.resultList.perssion != '?auth=zhhz') {
                this.flag = false;
            }
        }

        let param = {
            globalSeqNo: this.dataList['mainFlowNo']
        }
        this.customCenterHttpService.createGlobalSeqNo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                commonHeader['globalSeqNo'] = data.globalSeqNo;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.show(num)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    /** */
    //
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
        this.inValue = param.orgId;
    }
    //
    perTreeShow() {
        this.orgTreePage = true;
    }
    //
    perTreeCall(event) {
        this.personShow = false;
        console.log(event)
        this.personId_new = event[0].userLoginId;
        this.personName_new = event[0].userName;
        this.bussinessMap.specifyNextOpers = event[0].userLoginId;
        this.postName = event[0].postName;
        this.bussinessMap.post = event[0]['postId'];
        this.checkManager();
    }
    //
    perShow() {
        if (!this.inValue) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '!' });
            return;
        }
        this.personShow = true;
    }
}